<template>
    <div id="view-student">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{name}}</h4>
            </li>
            <li class="collection-item">Student Admission Number#: {{student_id}}</li>
            <li class="collection-item">Form : {{form}}</li>
            <li class="collection-item">Role : {{role}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button class="btn red" @click="deleteStudent">Delete</button>


        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-student', params: {student_id: student_id}}" class="btn-floating btn-large red">
            <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-student',
    data (){
        return {
            student_id: null,
            name: null,
            form: null,
            role:null
        }
    },
    beforeRouteEnter (to, from, next){
        db.collection('students').where('student_id', '==', to.params.student_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc=>{
                next(vm =>{
                    vm.student_id = doc.data().student_id
                    vm.name = doc.data().name
                    vm.form = doc.data().form
                    vm.role = doc.data().role
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('students').where('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.student_id =doc.data().student_id
                    this.name = doc.data().name
                    this.form = doc.data().form
                    this.role = doc.data().role
                })
            })
        },
        deleteStudent (){
            if(confirm('Are you sure you want to delete?')){
               db.collection('students').where('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })
            }) 
            }
        }
    }
}
</script>






