<template>
    <div id="edit-student">
        <h3>Edit Student</h3>
        <div class="row">
            <form @submit.prevent="updateStudent" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="student_id" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="form" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="role" required>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-student',
    data (){
        return {
            student_id: null,
            name: null,
            form: null,
            role: null
        }
    },
     beforeRouteEnter (to, from, next){
        db.collection('students').where('student_id', '==', to.params.student_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc=>{
                next(vm =>{
                    vm.student_id = doc.data().student_id
                    vm.name = doc.data().name
                    vm.form = doc.data().form
                    vm.role = doc.data().role
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('students').where('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.student_id =doc.data().student_id
                    this.name = doc.data().name
                    this.form = doc.data().form
                    this.role = doc.data().role
                })
            })
        },
        updateStudent () {
            db.collection('students').where('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        student_id: this.student_id,
                        name: this.name,
                        form: this.form,
                        role: this.role
                    })
                    .then(() => {
                        this.$router.push({name: 'view-student', params: {student_id: this.student_id}})
                    })
                })
            })
        }
    }
}
</script>






